import React from 'react'
import { Switch, Route } from 'react-router-dom'
import Index from './Index'
import TermsPrivacyAda from '../Footer/TermsPrivacyAda'
import Membership from './Membership'
import Join from './Join'
import Fitness from './Fitness'
import Classes from './Classes'
import PersonalTraining from './PersonalTraining'
import FreeTrial from './FreeTrial'
import About from './About'
import { MainContent } from './BasicComps'
import Contact from '../Footer/Contact';



const Main = () => (
	<MainContent>
		<Switch>
			<Route exact path='/' component={Index}/>
            <Route exact path='/terms-privacy-ada' component={TermsPrivacyAda}/>
			<Route exact path='/membership' component={Membership}/>
            <Route exact path='/membership/join' component={Join}/>
			<Route exact path='/fitness' component={Fitness}/>
            <Route exact path='/fitness/group-fitness' component={Classes}/>
            <Route exact path='/fitness/personal-training' component={PersonalTraining}/>
            <Route exact path='/free-trial' component={FreeTrial}/>
			<Route exact path='/about' component={About} />
			<Route exact path='/contact' component={Contact} />
		</Switch>
	</MainContent>
)
 export default Main