import React from 'react'
import { 
    PageTitle, 
    Hero, 
    HeroText, 
    BodyText,
    SecTitle,
    Input, 
    FormSec,
    FormDescContainer,
    Form,
    FormContainer, 
    ButtonContainer,
    PrimaryButton,
 } from '../Main/BasicComps'
import { MainContentContainer } from '../Main/BasicComps';

const ContactHero = () => (
    <Hero>
        <HeroText>
            <PageTitle>CONTACT US</PageTitle>
            <BodyText>Get in touch with us about a question, comment, or concern.</BodyText>
        </HeroText>
    </Hero>
)


class ContactUsForm extends React.Component {

    saveAndContinue = (e) => {
        e.preventDefault()
        if(this.props.values.firstName == '' || this.props.values.lastName == '' || this.props.values.zip == '' || this.props.values.phone == '' || this.props.values.email == '' || this.props.values.comment == '') {
            alert('Please fill out all of the information.')
        } else {
            this.props.nextStep()
        }
    }

    back  = (e) => {
        e.preventDefault();
        this.props.prevStep();
    }

    render() {
        const { values } = this.props;
        return(
            <FormContainer>
                <FormDescContainer>
                    <SecTitle>Contact Us</SecTitle>
                    <BodyText>Call: 999.555.1010</BodyText>
                    <BodyText>Email: NEFit****@gmail.com</BodyText>
                    <BodyText>Have a question? We're happy to answer it for you. We'll get back to you via email or phone within 1-2 business days.</BodyText>
                </FormDescContainer>
            <Form>
                <FormSec>
                    <Input placeholder='First Name' onChange={this.props.handleChange('firstName')} defaultValue={values.firstName} />
                    <Input placeholder='Last Name' onChange={this.props.handleChange('lastName')} defaultValue={values.lastName} />
                </FormSec>
                <FormSec>
                    <Input placeholder='Zip' onChange={this.props.handleChange('zip')} defaultValue={values.zip} />
                    <Input placeholder='Phone' onChange={this.props.handleChange('phone')} defaultValue={values.phone} />
                    <Input type='email' placeholder='Email' onChange={this.props.handleChange('email')} defaultValue={values.email} />
                </FormSec>
                <FormSec>
                    <Input placeholder='Comments' onChange={this.props.handleChange('comment')} defaultValue={values.comment} />
                </FormSec>
                <ButtonContainer>
                    <PrimaryButton onClick={this.saveAndContinue}>Submit</PrimaryButton>
                </ButtonContainer>
            </Form>
            </FormContainer>
        )
    }
}

class ContactConfirmation extends React.Component {


    render() {
        const { values } = this.props;
        return(
            <MainContentContainer>
                <SecTitle>Thank you, {values.firstName}!</SecTitle>
                <BodyText>We appreciate you reaching out to us. <br /> Please expect a response via email (at {values.email}) or by phone in 1-2 business days.</BodyText>
            </MainContentContainer>
        )
    }
}

class ContactForm extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
           step: 1,
           firstName: '',
           lastName: '',
           zip:'',
           email: '',
           phone: '',
           comment: '',
        }
    }

   nextStep = () => {
       const { step } = this.state
       this.setState({
           step : step + 1
       })
   }

   prevStep = () => {
       const { step } = this.state
       this.setState({
           step : step - 1
       })
   }

   handleChange = input => event => {
       this.setState({ [input] : event.target.value })
   }

   render(){
       const { step } = this.state;
       const { subType, firstName, lastName, address, phone, email, city, state, zip, bilFirstName, bilLastName, bilAddress, bilCity, bilState, bilZip, ccNum, expDate, CCV } = this.state;
       const values = { subType, firstName, lastName, address, phone, email, city, state, zip, bilFirstName, bilLastName, bilAddress, bilCity, bilState, bilZip, ccNum, expDate, CCV };
       switch(step) {
       case 1:
           return <ContactUsForm 
                   nextStep={this.nextStep} 
                   handleChange = {this.handleChange}
                   values={values}
                   />
       case 2:
           return <ContactConfirmation
                   values={values}
                   />
       }
   }
}

const Contact = () => (
    <>
    <ContactHero />
    <ContactForm />
    </>
)

export default Contact;