class SubscriptionType extends React.Component {
   
    saveAndContinue = (e) => {
        e.preventDefault()
        if (this.props.values.subType === '') {
            alert('Please choose a subscription option.')
        } else {
            this.props.nextStep()
        }
    }

    render(){
        return(
            <FormContainer>
            <Form>
                <SecTitle>Please select one.</SecTitle>
                <BodyText>Memberships come in two equal options, everything included, designed for simplicity.</BodyText>
                <FormSec>
                    <SubOptionContainer>
                        <InputRadio id='sub-monthly' name='suboptions' type='radio' value='Monthly: $39' onChange={this.props.handleChange('subType')} />  
                        <FormLabel htmlFor='sub-monthly'>
                        <GiantText>$49</GiantText>
                        Monthly <br />
                        </FormLabel>
                    </SubOptionContainer>
                    <SubOptionContainer>
                        <InputRadio id='sub-pif' name='suboptions' type='radio' value='Paid in Full: $500' onChange={this.props.handleChange('subType')} />
                        <FormLabel htmlFor='sub-pif'>
                        <GiantText>$500</GiantText>
                        Paid in Full <br />
                        </FormLabel>
                    </SubOptionContainer>
                </FormSec>
                    <PrimaryButton onClick={this.saveAndContinue}>Member Information</PrimaryButton>
            </Form>
            </FormContainer>
        )
    }
 }

class MemberInformation extends React.Component {

    saveAndContinue = (e) => {
        e.preventDefault()
        if(this.props.values.firstName == '' || this.props.values.lastName == '' || this.props.values.address == '' || this.props.values.city == '' || this.props.values.state == '' || this.props.values.zip == '' || this.props.values.phone == '' || this.props.values.email == '') {
            alert('Please fill out all of the information.')
        } else {
            this.props.nextStep()
        }
    }

    back  = (e) => {
        e.preventDefault();
        this.props.prevStep();
    }

    render() {
        const { values } = this.props;
        return(
            <FormContainer>
                <FormDescContainer>
                    <SecTitle>Member Information</SecTitle>
                    <BodyText>Please enter the member's information which will be attached to the membership account. This information can differ from the billing information on the next page.</BodyText>
                </FormDescContainer>
            <Form>
                <FormSec>
                    <Input placeholder='First Name' onChange={this.props.handleChange('firstName')} defaultValue={values.firstName} />
                    <Input placeholder='Last Name' onChange={this.props.handleChange('lastName')} defaultValue={values.lastName} />
                </FormSec>
                <FormSec>
                    <Input placeholder='Address' onChange={this.props.handleChange('address')} defaultValue={values.address} />
                </FormSec>
                <FormSec>
                    <Input placeholder='City' onChange={this.props.handleChange('city')} defaultValue={values.city} />
                    <Input placeholder='State' onChange={this.props.handleChange('state')} defaultValue={values.state} />
                    <Input placeholder='Zip' onChange={this.props.handleChange('zip')} defaultValue={values.zip} />
                </FormSec>
                <FormSec>
                    <Input placeholder='Phone' onChange={this.props.handleChange('phone')} defaultValue={values.phone} />
                    <Input placeholder='Email' onChange={this.props.handleChange('email')} defaultValue={values.email} />
                </FormSec>
                <ButtonContainer>
                    <SecondaryButton onClick={this.back}>Back</SecondaryButton>
                    <PrimaryButton onClick={this.saveAndContinue}>Billing Information</PrimaryButton>
                </ButtonContainer>
            </Form>
            </FormContainer>
        )
    }
}

