 class MembershipForm extends React.Component {
     constructor(props) {
         super(props);
         this.state = {
            step: 1,
            subType: '',
            firstName: '',
            lastName: '',
            address: '',
            city: '',
            state: '',
            zip:'',
            email: '',
            phone: '',
            bilFirstName: '',
            bilLastName: '',
            bilAddress: '',
            bilCity: '',
            bilState: '',
            bilZip: '',
            ccNum: '',
            expDate: '',
            CCV: '',
         }
     }

    nextStep = () => {
        const { step } = this.state
        this.setState({
            step : step + 1
        })
    }

    prevStep = () => {
        const { step } = this.state
        this.setState({
            step : step - 1
        })
    }

    handleChange = input => event => {
        this.setState({ [input] : event.target.value })
    }

    render(){
        const { step } = this.state;
        const { subType, firstName, lastName, address, phone, email, city, state, zip, bilFirstName, bilLastName, bilAddress, bilCity, bilState, bilZip, ccNum, expDate, CCV } = this.state;
        const values = { subType, firstName, lastName, address, phone, email, city, state, zip, bilFirstName, bilLastName, bilAddress, bilCity, bilState, bilZip, ccNum, expDate, CCV };
        switch(step) {
        case 1:
            return <SubscriptionType 
                    nextStep={this.nextStep} 
                    handleChange = {this.handleChange}
                    values={values}
                    />
        case 2:
            return <MemberInformation
                    nextStep={this.nextStep}
                    prevStep={this.prevStep}
                    handleChange = {this.handleChange}
                    values={values}
                    />
        case 3:
            return <BillingInformation 
                    nextStep={this.nextStep}
                    prevStep={this.prevStep}
                    handleChange = {this.handleChange}
                    values={values}
                    />
        case 4:
            return <MembershipConfirmation
                    nextStep={this.nextStep}
                    prevStep={this.prevStep}
                    values={values}
                    />
        
        case 5:
            return <MembershipReceipt
                    values={values}
                    />
        }
    }
 }






const Join = () => (

    <>
    <JoinHero />
    <MembershipForm />
    </>

)

export default Join