import React from 'react'
import { 
    PageTitle, 
    Form, 
    PrimaryButton, 
    Input, 
    FormSec,
    FormTitle, 
    Hero, 
    HeroText, 
    BodyText,
    SecTitle,
    MainContContainerCentered,
    MainContentContainer,
    SmImg,
 } from './BasicComps'
 import thankyou from '../../img/thank-you.svg'
 import trialhero from '../../img/hero-freetrial.png'
    

const FreeTrialHero = () => (
    <Hero src={trialhero}>
        <HeroText>
            <PageTitle>FREE TRIAL</PageTitle>
            <BodyText>Try us FREE for 7 days!</BodyText>
        </HeroText>
    </Hero>
)

class FreeTrialInfo extends React.Component {

    saveAndContinue = (e) => {
        e.preventDefault()
        if(this.props.values.firstName === '' || this.props.values.lastName === '' || this.props.values.address === '' || this.props.values.city === '' || this.props.values.state === '' || this.props.values.zip === '' || this.props.values.phone === '' || this.props.values.email === '') {
            alert('Please fill out all of the information.')
        } else {
            this.props.nextStep()
        }
    }

    render() {
        const { values } = this.props;
        return(
            <MainContentContainer>
            <Form>
                <FormTitle>Fill out your information to recieve a complimentary pass good for 7 days.</FormTitle>
                <FormSec>
                    <Input placeholder='First Name' onChange={this.props.handleChange('firstName')} defaultValue={values.firstName} />
                    <Input placeholder='Last Name' onChange={this.props.handleChange('lastName')} defaultValue={values.lastName} />
                </FormSec>
                <FormSec>
                    <Input placeholder='Address' onChange={this.props.handleChange('address')} defaultValue={values.address} />
                </FormSec>
                <FormSec>
                    <Input placeholder='City' onChange={this.props.handleChange('city')} defaultValue={values.city} />
                    <Input placeholder='State' onChange={this.props.handleChange('state')} defaultValue={values.state} />
                    <Input placeholder='Zip' onChange={this.props.handleChange('zip')} defaultValue={values.zip} />
                </FormSec>
                <FormSec>
                    <Input placeholder='Phone' onChange={this.props.handleChange('phone')} defaultValue={values.phone} />
                    <Input placeholder='Email' onChange={this.props.handleChange('email')} defaultValue={values.email} />
                </FormSec>
                <PrimaryButton onClick={this.saveAndContinue}>
                    Try Us Free
                </PrimaryButton>
                <BodyText>
                    * You agree to recieve correspondence from our staff and any marketing or newsletter material by submitting this form.
                </BodyText>
            </Form>
            </MainContentContainer>
        )
    }
}

class FreeTrialConfirmation extends React.Component {
    render() {
        const { values: { firstName, email } } = this.props;
        return(
            <MainContContainerCentered>
                <SmImg src={thankyou}/>
                <SecTitle>Thank you, {firstName}!</SecTitle>
                <p>
                    We'll send your free trial to {email} soon.
                </p>
            </MainContContainerCentered>
        )
    }
}


class FreeTrialForm extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
           step: 1,
           firstName: '',
           lastName: '',
           address: '',
           city: '',
           state: '',
           zip:'',
           email: '',
           phone: '',
        }
    }

   nextStep = () => {
       const { step } = this.state
       this.setState({
           step : step + 1
       })
   }

   handleChange = input => event => {
       this.setState({ [input] : event.target.value })
   }

   render(){
       const { step } = this.state;
       const { firstName, lastName, address, phone, email, city, state, zip} = this.state;
       const values = { firstName, lastName, address, phone, email, city, state, zip};
       switch(step) {
       case 1:
           return <FreeTrialInfo
                   nextStep={this.nextStep} 
                   handleChange = {this.handleChange}
                   values={values}
                   />
       case 2:
           return <FreeTrialConfirmation
                   values={values}
                   />
       }
   }
}

const FreeTrial = () => (
    <>
    <FreeTrialHero />
    <FreeTrialForm />
    </>
)

export default FreeTrial