const CarouselUI = ({position, total, handleClick, children}) => (
    <JumboCarouselContainer>
        <JumboCarouselChildren>
            {children}
        </JumboCarouselChildren>
        <JumboCarouselDotContainer>
            {Array(...Array(total)).map( (val, index) =>
            <JumboCarouselDot key={index} onClick={handleClick} data-position={index}>
                {index === position ? '● ' : '○ ' }
            </JumboCarouselDot> )}
        </JumboCarouselDotContainer>
    </JumboCarouselContainer>

) 

const JumboCarousel = makeCarousel(CarouselUI);

